                </v-btn>
                &nbsp; &nbsp; &nbsp; &nbsp;
                <v-btn style="width: 250px" color="bg-primary text-white" class="border-radius-xl">
                    <v-icon left> control_point </v-icon>
                    เพิ่มการนัดหมาย
                </v-btn>
            </v-row>
        </v-footer> -->
