</template>

<script>
import CheckmongHomeBar from "@/components/CheckmongHomeBar.vue";
import {
    APIURL
} from '@/constants'
import {
    LIFFID
} from '@/constants'
import Vue from 'vue'
import WaitingModal from "@/components/WaitingModal.vue";
export default {
    name: "calendar-patient",
    components: {
        CheckmongHomeBar,
        WaitingModal
    },
    data: () => ({
        page: 1,
        pageCount: 0,
        itemsPerPage: 10,
        cf_dialog: false,
        patient_list: [],
        active_waiting: false,
        title: '',
        status: '',
        search: '',
        attrs: "",
        on: "",
        selected_row: "",
        dialog: false,
        arrayEvents: null,
        bg_sound: new Audio(require('@/assets/img/checkmong/azq/audio/bg_sound.mp3')),
        date1: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
            .toISOString()
            .substr(0, 10),
        date2: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
            .toISOString()
            .substr(0, 10),

        headers: [{
                text: "ลำดับ",
                value: "id",
            },
            {
                text: "ชื่อ-นามสกุล",
                value: "patient_name",
            },
            {
                text: "",
                value: "line_status",
            },
            {
                text: "",
                value: "action",
                sortable: false
            },
        ],
        // patient_list: [{
        //         id: "1",
        //         patient_name: "ชัชวาลย์ รัตนโสภา",
        //         line_status: true,
        //         action: false
        //     },
        //     {
        //         id: "2",
        //         patient_name: "ลลิตา รัตนโสภา",
        //         line_status: false,
        //         action: true
        //     },
        // ],
    }),
    async beforeCreate() {
        window.liff.ready
        // await window.liff.init({
        //     liffId: LIFFID
        // }).then(() => {
        //     //console.log('done window.liff.init')
        // });
    },
    mounted() {

        this.getPatientList()
    },
    watch: {
        menu(val) {
            val && setTimeout(() => (this.activePicker = "YEAR"));
        },
    },
    methods: {
        del_user(val) {
            this.selected_row = val
            this.cf_dialog = true
        },
        async delRelation() {
            //console.log('his.selected_row.user_relation_id:',this.selected_row.user_relation_id)
            this.active_waiting = true
            this.title = 'กำลังลบข้อมูล',
                this.status = 'loading'
            await Vue.axios.post(APIURL + '/del_relation', {
                "user_relation_id": this.selected_row.user_relation_id
            }, {
                headers: {
                    'Content-Type': 'application/json'
                }
            }).then((response) => {
                if (response.data) {
                    this.active_waiting = false
                    this.dialog_edit = false
                    this.dialog = false
                    // this.getBookingList()
                    this.$router.go()

                    // this.$store.commit('setUserInfo',response.data);
                    // this.district = response.data.district
                    // this.subdistrict_list = response.data.location_list
                    // this.location_id = response.data.location_list
                } else {
                    this.active_waiting = false

                }

            })
        },

        async getPatientList() {
            this.active_waiting = true
            this.title = 'กำลังอัพเดต',
                this.status = 'loading'
            await Vue.axios.post(APIURL + '/get_patient_list', {
                "user_id": this.$store.state.login_detail.user_id,
            }, {
                headers: {
                    'Content-Type': 'application/json'
                }
            }).then((response) => {
                //console.log(response)
                if (response.data) {
                    this.active_waiting = false
                    //console.log(response.data)
                    this.patient_list = response.data.patient_list;
                    //console.log('777777777777777')
                    //console.log(this.patient_list)
                } else {
                    this.active_waiting = false
                }

            })

        },
        update_status_wait(val) {
            this.active_waiting = val
        },
        handleClick(value) {
            this.selected_row = value;
            this.dialog = true;
        },
        async getUserInfo(val) {
            // //console.log('this.$store.state.login_detail.user_id:',this.$store.state.login_detail.user_id)
            await Vue.axios.post(APIURL + '/get_user_info', {
                "user_id": val
            }, {
                headers: {
                    'Content-Type': 'application/json'
                }
            }).then((response) => {
                //console.log('get_user_info',response)
                if (response.data) {
                    //console.log('get_user_info',response.data)
                    this.active_waiting = false
                    // this.$store.commit('setUserInfo',response.data);
                    this.$store.commit('setQrPatientId', (response.data.user_id).slice(0, 7))
                    this.$store.commit('setQrPatientInfo', response.data)
                    // this.$router.push('/view_patient_health_record_no_line')
                    this.$store.commit('setAzq_type', {
                        'azq_type': 'azq_public'
                    });
                    this.$router.push('/azq_provider')
                    // this.province = response.data.province
                    // this.district = response.data.district
                    // this.subdistrict_list = response.data.location_list
                    // this.location_id = response.data.location_list
                    // this.$router.push('/home_checkmong')
                } else {
                    this.active_waiting = false

                }

            })

        },
        goAzq(val) {
            // this.$router.push('/azq') 
            //console.log(val)
            this.setBgSound()
            this.getUserInfo(val.user_id)
        },
        toggleApi() {
            //console.log('77777toggleApi')
            this.$fullscreen.toggle()
            this.hidden = !this.hidden
        },
        setBgSound() {
            //     const azq_ans_temp = this.$store.state.azq_ans
            //     this.$store.commit('setAzqAns', {
            //     ...azq_ans_temp,
            //     'bg_sound': this.bg_sound,
            //     'full_name':this.$store.state.qr_patient_info.fullname,
            //     'phone_number':this.$store.state.qr_patient_info.phone_number,
            //     'age':this.$store.state.qr_patient_info.age,
            //     'user_id':this.$store.state.qr_patient_info.user_id

            // });

            // // this.azq_ans_temp = this.$store.state.azq_ans
            // //     this.azq_ans_temp.s_pdpa = this.pdpa_accept
            // //     this.azq_ans_temp.current_s = 's_pdpa'
            // //     //console.log(this.azq_ans_temp)
            // //     // //console.log(current_ans)
            // //     // this.play(this.bg_sound)
            // //     this.$store.commit('setAzqAns', {
            // //         ...this.azq_ans_temp
            // //     });
            // this.play(this.$store.state.azq_ans.bg_sound)

        },
        play(audio) {
            audio.isPlaying = true;
            if (typeof audio.loop == 'boolean') {
                audio.loop = true;
            } else {
                audio.addEventListener('ended', function () {
                    this.currentTime = 0;
                    this.play();
                }, false);
            }
            audio.play();
        },

        pause(audio) {
            audio.isPlaying = false;
            audio.pause();
            audio.currentTime = 0;
        },
    },
};
</script>

